import React from 'react'
import FileDnd from './FileDnd'
import { Grid, Row, Panel, Col, Button } from 'react-bootstrap'
import FontAwesome from 'react-fontawesome'


export default class MyProjects extends React.Component{


  render(){
    return (
      <div className="container-fluid">

        <Row>
          <Col xs={2}>
            <FileDnd></FileDnd>
          </Col>
        </Row>

        <Row>
          <Col xs={5}>
            <Button>
              <FontAwesome className="">
                Some Text
              </FontAwesome>
            </Button>
          </Col>
        </Row>


        <div className="row">
          <div className="col-5">
            <button type="button" className="btn btn-primary">
              <i className="fa fa-plus aria-hiden="true></i>
              Create New Project
            </button>
          </div>
        </div>

        <div className="row">
          <div id='dndbox' className="col-5 push-4">
            <p className="text-center">Drag and Drop File</p>
          </div>
        </div>

        <div className="row">
          <Col xs={6}>
            <Panel bsStyle={'success'}></Panel>
          </Col>
        </div>


        <div className="row">
          <div className="col-2 push-10">
            <button type="button" className="btn btn-primary">
              <i className="fa fa-envelope-o" aria-hiden="true"></i>
               Email Project
            </button>
          </div>
        </div>
    </div>
    )
  }
}



